import androidx.compose.runtime.CompositionLocalProvider
import com.varabyte.kobweb.core.AppGlobals
import com.varabyte.kobweb.navigation.RoutePrefix
import com.varabyte.kobweb.navigation.Router
import com.varabyte.kobweb.navigation.UpdateHistoryMode
import com.varabyte.kobweb.silk.components.animation.registerKeyframes
import com.varabyte.kobweb.silk.defer.renderWithDeferred
import kotlinx.browser.document
import kotlinx.browser.window
import org.jetbrains.compose.web.renderComposable

public fun main() {
    RoutePrefix.set("")
    val router = Router()
    com.varabyte.kobweb.core.init.initKobweb(router) { ctx ->
        ctx.router.register("/") { org.example.personalportfolio.pages.HomePage() }

    }
    router.addRouteInterceptor {
        path = path.removeSuffix(".html").removeSuffix(".htm")
    }

    com.varabyte.kobweb.silk.init.additionalSilkInitialization = { ctx ->
        com.varabyte.kobweb.silk.init.initSilkWidgets(ctx)
        com.varabyte.kobweb.silk.init.initSilkWidgetsKobweb(ctx)
        ctx.theme.registerComponentStyle(com.binayshaw7777.kotfolio.components.widgets.ArrowUpStyle)
        ctx.theme.registerComponentStyle(com.binayshaw7777.kotfolio.components.widgets.BackToTopButtonStyle)
        ctx.theme.registerComponentStyle(org.example.personalportfolio.styles.NavigationItemStyle)
        ctx.theme.registerComponentStyle(org.example.personalportfolio.styles.ButtonStyle)
        ctx.theme.registerComponentStyle(org.example.personalportfolio.styles.SocialLinkStyle)
        ctx.theme.registerComponentStyle(org.example.personalportfolio.styles.PresentationColumnStyle)
        ctx.theme.registerComponentStyle(org.example.personalportfolio.styles.SkillStyle)
        ctx.theme.registerComponentStyle(org.example.personalportfolio.styles.AcademicStyle)
        ctx.theme.registerComponentStyle(org.example.personalportfolio.styles.PortfolioStyle)
        ctx.theme.registerComponentStyle(org.example.personalportfolio.styles.FormStyle)
        ctx.theme.registerComponentVariants(org.example.personalportfolio.styles.CircleButtonVariant)
        ctx.stylesheet.registerKeyframes(com.binayshaw7777.kotfolio.components.widgets.FadeInKeyFrames)
        ctx.stylesheet.registerKeyframes(org.example.personalportfolio.styles.AppearMoveKeyFrames)
        ctx.stylesheet.registerKeyframes(org.example.personalportfolio.styles.DisappearParkKeyFrames)
        ctx.stylesheet.registerKeyframes(org.example.personalportfolio.styles.ShowKeyFrames)
        ctx.stylesheet.registerKeyframes(org.example.personalportfolio.styles.VanishKeyFrames)
        org.example.personalportfolio.initializeBreakpoints(ctx)
    }

    // For SEO, we may bake the contents of a page in at build time. However, we will
    // overwrite them the first time we render this page with their composable, dynamic
    // versions. Think of this as poor man's hydration :)
    // See also: https://en.wikipedia.org/wiki/Hydration_(web_development)
    router.navigateTo(window.location.href.removePrefix(window.location.origin),
            UpdateHistoryMode.REPLACE)

    val root = document.getElementById("root")!!
    while (root.firstChild != null) { root.removeChild(root.firstChild!!) }

    AppGlobals.initialize(mapOf("title" to "PersonalPortfolio"))
    renderComposable(rootElementId = "root") {
        org.example.personalportfolio.MyApp {
            router.renderActivePage { renderWithDeferred { it() } }
        }
    }
}
